//layout mixins - start

@mixin qodefTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin qodefTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin qodefRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin qodefAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin qodefTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

//layout mixins - end

//transition mixins - start

@mixin qodefTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin qodefTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin qodefTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin qodefAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin qodefTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

//transition mixins - end

//checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;
%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;
    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }
    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }
    input[type=checkbox] + label span.qodef-label-text {
        padding-left: 10px;
        display: inline-block;
        line-height: $checkbox-size;
        color: $default-text-color;
    }
    input[type=checkbox] + label .qodef-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid $default-border-color;
        background-color: $default-background-color;
        border-radius: 2px;
        &:hover {
            cursor: pointer;
        }
    }
    input[type=checkbox] + label .qodef-label-view:after {
        content: '';
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        background-color: $first-main-color;
        opacity: 0;
        @include qodefTransition(opacity 0.3s ease-in-out);
    }
    input[type=checkbox]:checked + label .qodef-label-view:after {
        opacity: 1;
    }
}

//checkbox mixins - end

/* common mixins - start */

@mixin qodefBckImageStyle(){
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin qodefImageOverlayHoverStyle($with-hover: true){
    
    @if ($with-hover) {
        
        &:hover {
        
            &:after {
                opacity: 1;
            }
        }
    
        &:after {
            @include qodefAbsoluteHolderLayout();
            content: '';
            background-color: rgba(#fff, .4);
            opacity: 0;
            @include qodefTransition(opacity .2s ease-in-out);
        }
        
    } @else {
        @include qodefAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-text-color, .4);
        opacity: 0;
        @include qodefTransition(opacity .2s ease-in-out);
    }
}

@mixin qodefButtonDefaultStyle() {
    font-family: inherit;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    border-radius: 25px;
    width: auto;
    outline: none;
    font-size: 11px;
    line-height: 2em;
    letter-spacing: 0.2em;
    font-weight: 600;
    text-transform: uppercase;
    box-sizing: border-box;
    margin: 0;
    @include qodefTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin qodefButtonTransparentColor() {
    color: $additional-dark-color;
    background-color: transparent;
}

@mixin qodefButtonSolidColor() {
    color: #fff;
    background-color: $additional-dark-color;
    border: 1px solid transparent;
}

@mixin qodefButtonSolidHoverColor() {
    color: #fff;
    background-color: $additional-dark-color;
}

@mixin qodefButtonOutlineColor() {
    color: $additional-dark-color;
    background-color: transparent;
    border: 1px solid $additional-dark-color;
}

@mixin qodefButtonOutlineHoverColor() {
    color: #fff;
    background-color: $additional-dark-color;
    border-color: $additional-dark-color;
}

@mixin qodefButtonSmallParams() {
    padding: 12px 36px;
}

@mixin qodefButtonMediumParams() {
    padding: 15px 43px;

    &.qodef-btn-dessau{
        padding: 0;
        min-height: 38px;
    }
}

@mixin qodefButtonLargeParams() {
    padding: 15px 66px;
}

@mixin qodefButtonHugeParams() {
    display: block;
    text-align: center;
    padding: 15px 26px;
}

@mixin qodefPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }
    &:-moz-placeholder {
        @content;
        opacity: 1;
    }
    &::-moz-placeholder {
        @content;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

/* common mixins - end */

/* mixins styles - start */

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 20px;
    padding: 13px 5px 13px 0;
    font-family: inherit;
    font-size: 15px;
    line-height: 20px;
    font-weight: inherit;
    color: $default-text-color;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    cursor: text;
    box-sizing: border-box;
    @include qodefTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-text-color;
        border-color: $default-text-color;
    }

    @include qodefPlaceholder {
        color: inherit;
    }
}

/* mixins styles - end */

